import fetch from '@/utils/fetch';

export default{
    /**/
    create(data){
        return fetch({
            url: '/project',
            method: 'post',
            data: data
        });
    },
    /**/
    getList(data){
        return fetch({
            url: '/project',
            method: 'get',
            params: data
        });
    },
    /*ID*/
    getById(data){
        return fetch({
            url: '/project/' + data,
            method: 'get',
        });
    },
    /**/
    update(data){
        return fetch({
            url: '/project',
            method: 'put',
            data: data
        });
    },
    /**/
    delivery(data){
        return fetch({
            url:'/project/delivery',
            method:'post',
            data:data
        });
    },
    /*ID*/
    getSampleCount(data){
        return fetch({
            url: '/project/' + data+'/sampleCount',
            method: 'get',
        });
    },
}




